async function countAllSn() {
    // snDb.info().then(info =>
    //     store.dispatch('updateContainsData',{count: info.doc_count})
    // ); //dispatch to the setProgress action in the store
    snDb.info().then(function(info) {
        const  snTotal = info.doc_count;
        console.log("countAllSn sn count returns " + snTotal);
        if (snTotal && snTotal>0) { //service numbers found
            updateAccountSummaryChart();
            store.dispatch('updateContainsData', {count: snTotal}).then(function(){
            });
        } else {
            store.dispatch('updateContainsData', {count: 0});
            store.dispatch('setProgress',false); //dispatch to the setProgress action in the store
        }
       });

    //console.log(info.doc_count));
}


