async function bulkPurgeAll(customerNumber, accountNumber) {

    let snResult = snDb.allDocs({include_docs: true}).then(allDocs => {
        return allDocs.rows.map(row => {
            return {_id: row.id, _rev: row.doc._rev, _deleted: true};
        });
    }).then(deleteDocs => {
        return snDb.bulkDocs(deleteDocs).then((res) => {
            console.log("bulk purge all snDb " );
            return res;
            //successfully deleted now insert the new list of callMonth
        }).catch((err) => {
            console.error(err);
        });
    }).then((res) => {
        return snUsageDb.allDocs({include_docs: true}).then(allDocs => {
            return allDocs.rows.map(row => {
                return {_id: row.id, _rev: row.doc._rev, _deleted: true};
            });
        })
    }).then((deleteSnUsage) => {
            return snUsageDb.bulkDocs(deleteSnUsage).then((res) => {
                //console.log(res);
                console.log("purged all snUsageDb ");
                countAllSn();
                store.dispatch('setProgress',false); //dispatch to the setProgress action in the store
                return res;
                //successfully deleted now insert the new list of callMonth
            }).catch((err) => {
                console.error(err);
            });
        });
        // let snUsageResult = snUsageDb.allDocs({include_docs: true}).then(allDocs => {
        //     return allDocs.rows.map(row => {
        //         return {_id: row.id, _rev: row.doc._rev, _deleted: true};
        //     });
        // }).then(deleteDocs => {
        //     snUsageDb.bulkDocs(deleteDocs).then((res) => {
        //         console.log("purged all snUsageDb");
        //         //successfully deleted now insert the new list of callMonth
        //     }).catch((err) => {
        //         console.error(err);
        //     });
        // });

        return Promise.all([snResult]);

}

