import  Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);


export default new Vuex.Store({
    state: {
        isShowProgress : false,
        rowsRead: 0,
        containsData: false,
        accountSummaryChart: {
            series: [],
            labels: []
        },
        snScatterDomesticDataDatePreset: {
            lastMonth: {
                start: 201111,
                end: 201112,
            },
            lastQuarter: {
                start: 201109,
                end: 201112,
            },
            lastYear: {
                start: 201012,
                end: 201112,
            }

        },
        snScatterChart:{
            series:[
                {
                    name: 'local calls',
                    data: [
                        {x: 100, y: 53},
                        {x: 200, y: 60},
                        {x: 250, y: 23},
                        {x: 280, y: 83}
                    ]
                },
            ],
            labels:[]
        },
    },
    getters: {
        // Here we will create a getter
        isShowProgress: state =>  state.isShowProgress
        ,
        rowsRead: state =>  state.rowsRead
        ,
        accountSummaryChart: state =>  state.accountSummaryChart
        ,
        snScatterChart: state =>  state.snScatterChart
        ,
        snScatterDomesticDataDatePreset: state => state.snScatterDomesticDataDatePreset
        ,
        containsData: state =>  state.containsData

    },

    mutations: {
        // Here we will create Jenny
        // Jenny
        commitProgress(state, progress) {
            console.log("commit progress is "+ progress);
            state.isShowProgress = progress
        },
        commitContainsData(state, containsData) {
            state.containsData = containsData;
        },
        commitRowsRead(state, count) {
            state.rowsRead = count
        },
        commitAccountSummaryChart(state,data) {
          state.accountSummaryChart = data;
        },
        commitDomesticDataScatterChart(state,data) {
            state.snScatterChart = data;
        },
        commitDomesticDataScatterDatePreset(state,data) {
            state.snScatterDomesticDataDatePreset = data;
        }
        },
    actions: {
        updateContainsData(context,data) {
            console.log("updateContainsData returns " + data.count);
            context.commit('commitContainsData',data.count>0);
            if (data.count>0) {
                context.commit('commitRowsRead',data.count);
            } else {
                context.commit('commitRowsRead',0);
            }
        },
        updateAccountSummaryChart(context, data) {
            context.commit('commitAccountSummaryChart',data);
        },
        updateSnScatterChart(context, data) {
            context.commit('commitDomesticDataScatterChart',data);
        },
        updateDomesticDataScatterDatePreset(context,data) {
            context.commit('commitDomesticDataScatterDatePreset',data);
            console.log("updateDomesticDataScatterDatePreset updated ");
        },
        setProgress(context, progress) {
            console.log("action progress is "+ progress);
            context.commit('commitProgress',progress);
        }


    }
});