
import store from '../store/store';

import moment from 'moment';
import {prepareSnUsage} from "@/api/PouchDao";

const DomesticDataReport = {
    filterByLastMonth() {
        const dateRange = store.getters.snScatterDomesticDataDatePreset.lastMonth;
        return this.prepareSnUsageScatterChart(dateRange.start,dateRange.end,'DOMESTIC VOICE');
    },
    filterByLastQuarter() {
        const dateRange = store.getters.snScatterDomesticDataDatePreset.lastQuarter;
        return this.prepareSnUsageScatterChart(dateRange.start,dateRange.end,'DOMESTIC VOICE');
    },
    filterByLastYear() {
        const dateRange = store.getters.snScatterDomesticDataDatePreset.lastYear;
        return this.prepareSnUsageScatterChart(dateRange.start,dateRange.end,'DOMESTIC VOICE');
    },
    async prepareSnUsageScatterChart(startYYYYMM, endYYYYMM, category) {
        const result = await  prepareSnUsage(startYYYYMM,endYYYYMM);
        //console.log("prepareSnUsageScatterChart result is ");
        //console.log(result);
        //format the result to update the store...
        /**
         data: [
         {x: 100, y: 53},
         {x: 200, y: 60},
         {x: 250, y: 23},
         {x: 280, y: 83}
         ]

         *
         */
        // let data = [];
        // result.docs.forEach((snUsage) => {
        //     if (snUsage.category == category) {
        //         //console.log(snUsage);
        //         const usage = {meta: snUsage.sn, x: snUsage.call, y: snUsage.duration};
        //         data.push(usage);
        //     }
        // });

        /**
         * more smart is to make them into series eg for each month....
         */
        let dataYearMonthMap = new Map(); //key to be the last month
        result.docs.forEach((snUsage) => {
            if (snUsage.category == category) {
                //console.log(snUsage);
                const usage = {meta: snUsage.sn, x: snUsage.call, y: snUsage.duration};
                if (dataYearMonthMap.get(snUsage.yearMonth)) {
                    dataYearMonthMap.get(snUsage.yearMonth).push(usage);
                } else {
                    const data = [];
                    data.push(usage);
                    dataYearMonthMap.set(snUsage.yearMonth,data);

                }
            }
        });
        console.log("dataYearMonth is " + dataYearMonthMap.size);
        //now that we got the group, iterate through to create the series...
        let seriesData = [];
        dataYearMonthMap.forEach((value,key) => {
            seriesData.push({
                name: key + "",
                data: value,
            })
        });
        console.log(seriesData);

        if (result.docs.length == 0) {
            console.warn("no "+ category +" found between " + startYYYYMM + " and " + endYYYYMM);
        }
        // const scatterChart = {
        //     series:[
        //         {
        //             name: 'local calls',
        //             data: data,
        //         },
        //     ],
        //     labels:[]
        // };
        const scatterChart = {
            series: seriesData,
            labels:[]
        };
        await store.dispatch("updateSnScatterChart",scatterChart);
        return seriesData;
    }

}

export default DomesticDataReport;
